html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: bold;
}
dfn {
  font-style: italic;
}
h1 {
  font-size: 2em;
  margin: 0;
}
mark {
  background: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 0;
}
hr {
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type='button'],
input[type='reset'],
input[type='submit'] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type='checkbox'],
input[type='radio'] {
  box-sizing: border-box;
  padding: 0;
}
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  height: auto;
}
input[type='search'] {
  -webkit-appearance: textfield;
  /* 1 */
  box-sizing: content-box;
  /* 2 */
}
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  /* 1 */
  padding: 0;
  /* 2 */
}
textarea {
  overflow: auto;
  resize: none;
}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
body {
  transition: 0.3s;
  position: relative;
  background-color: #3b444c;
}
body.no-scroll {
  overflow: hidden;
}
body.opacity-layer {
  position: relative;
  transition: 0.3s;
}
body.opacity-layer:after {
  content: '';
  opacity: 0.3;
  position: absolute;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 2;
  transition: 0.3s;
}
@-ms-viewport {
  width: device-width;
}
.wrapper {
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1200px;
  width: 100%;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
/*=====================
  Базовая типография
=====================*/
@font-face {
  font-family: 'Ubuntu';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/ubuntu/v15/4iCv6KVjbNBYlgoC1CzTtw.ttf) format('truetype');
}
@font-face {
  font-family: 'Ubuntu';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/ubuntu/v15/4iCs6KVjbNBYlgo6eA.ttf) format('truetype');
}
@font-face {
  font-family: 'Ubuntu';
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/ubuntu/v15/4iCv6KVjbNBYlgoCjC3Ttw.ttf) format('truetype');
}
@font-face {
  font-family: 'Ubuntu';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/ubuntu/v15/4iCv6KVjbNBYlgoCxCvTtw.ttf) format('truetype');
}
body {
  font-family: 'Ubuntu', sans-serif;
  font-size: 15px;
  line-height: 1.2;
  font-weight: 400;
  color: #fff;
}
a {
  color: inherit;
  text-decoration: none;
}
img {
  max-width: 100%;
}
p,
li {
  line-height: 1.2;
}
p:last-child,
li:last-child {
  margin-bottom: 0;
}
.header {
  padding: 16px 0 27px;
  border-bottom: 1px solid #4c5761;
}
.header__wrap {
  display: flex;
  position: relative;
}
.header .logo {
  margin-right: 74px;
}
.header .logo p {
  font-weight: 500;
  font-size: 40px;
  margin: 0;
}
.header .logo p.sub {
  font-size: 15px;
  letter-spacing: 6.5px;
  text-align: center;
}
.header .logo .green {
  color: #14bed3;
}
.header .logo .violet {
  color: #c590f8;
}
.header__feedback {
  margin: 10px 0 0 auto;
  background-color: #14bed3;
  color: #3b444c;
  font-weight: 500;
  padding: 6px 14px;
  border-radius: 100px;
  align-self: flex-start;
}
.login {
  margin-top: 10px;
}
.login__wrap {
  display: flex;
}
.login__item {
  width: 100%;
  max-width: 180px;
  margin-right: 2px;
}
.login .input {
  font-size: 12px;
  background-color: transparent;
  color: #fff;
  font-weight: 300;
  border: 1px solid #767c82;
  border-radius: 100px;
  padding: 9px 12px 8px;
  width: 100%;
}
.login .input::placeholder {
  color: #fff;
  font-family: 'Ubuntu', sans-serif;
  font-size: 12px;
}
.login__submit {
  width: 32px;
  height: 32px;
  border-radius: 100px;
  background-color: #3f4a53;
  border: 1px solid transparent;
}
.login__submit svg {
  fill: #14bed3;
  width: 16px;
  height: 12px;
}
.login .link {
  font-size: 12px;
  color: #14bed3;
  text-decoration: underline;
  letter-spacing: -0.15px;
  margin: 7px 12px 0 8px;
}
.intro {
  padding: 30px 0 28px;
  border-bottom: 1px solid #4c5761;
}
.intro__wrap {
  display: flex;
}
.intro__main {
  width: 69%;
  padding-right: 40px;
}
.intro__main .banner {
  position: relative;
  padding: 50px 50px 36px;
}
.intro__main .bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.intro__main .bg img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.intro__main .icon {
  position: absolute;
  width: 48px;
  height: 48px;
  top: 33px;
  right: 24px;
}
.intro__main .icon svg {
  fill: #fff;
  width: 48px;
  height: 48px;
}
.intro__main .label {
  font-size: 12px;
  text-transform: uppercase;
  color: #fff;
  font-weight: 700;
  margin: 0 0 27px;
}
.intro__main .name {
  font-size: 30px;
  font-weight: 500;
  margin: 0 0 27px;
  max-width: 500px;
}
.intro__main .link {
  font-size: 15px;
  font-weight: 700;
  color: #3b444c;
  background-color: #fff;
  border-radius: 100px;
  display: inline-block;
  padding: 10px 10px;
  width: 100%;
  max-width: 224px;
  text-align: center;
}
.intro__side {
  width: 31%;
}
.intro__side .banner {
  position: relative;
  display: flex;
  padding: 20px;
}
.intro__side .banner:not(:last-of-type) {
  margin-bottom: 19px;
}
.intro__side .banner .bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.intro__side .banner .bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.intro__side .banner .image {
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  padding: 5px;
}
.intro__side .banner .text {
  width: calc(100% - 100px);
  padding-left: 20px;
}
.intro__side .banner .text .name {
  font-size: 16px;
  font-weight: 500;
  color: #fff;
  margin: 10px 0 7px;
}
.intro__side .banner .text p {
  font-size: 13px;
  color: #fff;
  margin: 0;
}
.links {
  padding: 30px 0;
  border-bottom: 1px solid #4c5761;
}
.links__wrap {
  display: flex;
  margin: 0 -20px;
}
.links__item {
  width: calc(100% - 40px);
  margin: 0 20px;
  background-color: #303840;
  padding: 33px 37px;
}
.links__item .name {
  font-size: 17px;
  font-weight: 700;
  margin: 0 0 28px;
  display: flex;
  align-items: center;
}
.links__item .name svg {
  width: 24px;
  height: 24px;
  margin-right: 17px;
}
.links__item .text {
  color: #fff;
  font-size: 13px;
  margin: 0 0 31px;
}
.links__item .link {
  font-size: 12px;
  font-weight: 300;
  color: #3b444c;
  display: inline-block;
  padding: 3px 8px;
  border-radius: 100px;
  margin-left: -5px;
}
.links__item:nth-of-type(1) .name {
  color: #14bed3;
}
.links__item:nth-of-type(1) .name svg {
  fill: #14bed3;
}
.links__item:nth-of-type(1) .link {
  background-color: #14bed3;
}
.links__item:nth-of-type(2) .name {
  color: #c590f8;
}
.links__item:nth-of-type(2) .name svg {
  fill: #c590f8;
}
.links__item:nth-of-type(2) .link {
  background-color: #c590f8;
}
.links__item:nth-of-type(3) .name {
  color: #9095f8;
}
.links__item:nth-of-type(3) .name svg {
  fill: #9095f8;
}
.links__item:nth-of-type(3) .link {
  background-color: #9095f8;
}
